import { useAppSelector } from '../store/configureStore';
import { useEffect } from 'react';
import { fetchProductsAsync, fetchFilterAsync, productSelectors } from '../../Features/Catalog/catalogSlice';
import { useAppDispatch } from '../store/configureStore';

export default function useProducts(){
    const products = useAppSelector(productSelectors.selectAll);
    const dispatch = useAppDispatch();
    const { productsLoaded, filtersLoaded, brands, types,metaData } = useAppSelector((state) => state.catalog);
    useEffect(() => {
      if (!productsLoaded) {
        dispatch(fetchProductsAsync());
      }
    }, [productsLoaded, dispatch]);
    useEffect(() => {
      if (!filtersLoaded) {
        dispatch((fetchFilterAsync()));
      }
    }, [dispatch, filtersLoaded])

    return {
        products,
        productsLoaded,
        filtersLoaded,
        brands,
        types,
        metaData,
    }
}