//import { yupResolver } from '@hookform/resolvers/yup';
import useProducts from '../../App/hooks/useProducts';
import { useEffect } from 'react';
//import agent from '../../App/api/agent';
import { Box } from '@mui/material';
import { Typography } from '@mui/material';
import { Paper } from '@mui/material';
import { Grid } from '@mui/material';
import AppTextInput from '../../App/components/AppTextInput';
import { Button } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { useAppDispatch } from '../../App/store/configureStore';
import { useForm } from 'react-hook-form';
import AppSelectList from '../../App/components/AppSelectList';
import AppDropzone from '../../App/components/AppDropzone';
// import {yupResolver} from "@hookform/resolvers/yup"
// import { validationSchema } from './productValidation';
import agent from '../../App/api/agent';
import { setProduct } from '../Catalog/catalogSlice';

export default function ProductForm({ product, cancelEdit }) {
    const { control, reset, handleSubmit, watch, formState: { isDirty, isSubmitting } } = useForm({
        //resolver: yupResolver(validationSchema)
    });
    const { brands, types } = useProducts();
    const watchFile = watch('file', null);
    const dispatch = useAppDispatch();

    useEffect(() => {
        if (product && !watchFile && !isDirty) reset(product);
        return () => {
            if (watchFile) URL.revokeObjectURL(watchFile.preview);
        }
    }, [product, reset, watchFile, isDirty])

    async function handleSubmitData(data) {
            try{
                let response;
                if (product) {
                    response = await agent.Admin.updateProduct(product.id, data);
                    console.log(data);

                }
                else {
                    response = await agent.Admin.createProduct(data);
                    console.log(data);

                }
                dispatch(setProduct(response));
                cancelEdit();
                console.log(data);
            }catch(error){
                console.log(error);
                console.log(data);
            }
    }

    return (
        <Box component={Paper} sx={{ p: 4 }}>
            <Typography variant="h4" gutterBottom sx={{ mb: 4 }}>
                Product Details
            </Typography>
            <form onSubmit={handleSubmit(handleSubmitData)}>
                <Grid container spacing={3}>
                    <Grid item xs={12} sm={12}>
                        <AppTextInput control={control} name='name' label='Product name' />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <AppSelectList control={control} items={brands} name='brand' label='Brand' />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <AppSelectList control={control} items={types} name='type' label='Type' />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <AppTextInput type='number' control={control} name='price' label='Price' />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <AppTextInput type='number' control={control} name='quantityInStock' label='Quantity in Stock' />
                    </Grid>
                    <Grid item xs={12}>
                        <AppTextInput control={control} multiline={true} rows={4} name='description' label='Description' />
                    </Grid>
                    <Grid item xs={12}>
                        <Box display='flex' justifyContent='space-between' alignItems='center'>
                            <AppDropzone control={control} name='file' />
                            {watchFile ? (
                                <img src={watchFile.preview} alt="preview" style={{ maxHeight: 200 }} />
                            ) : (
                                <img src={product?.pictureUrl} alt={product?.name} style={{ maxHeight: 200 }} />
                            )}
                        </Box>
                    </Grid>
                </Grid>
                <Box display='flex' justifyContent='space-between' sx={{ mt: 3 }}>
                    <Button onClick={cancelEdit} variant='contained' color='inherit'>Cancel</Button>
                    <LoadingButton loading={isSubmitting} type='submit' variant='contained' color='success'>Submit</LoadingButton>
                </Box>
            </form>
        </Box>
    );
}
